html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1185px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  margin-top: 20px;
}

.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__top-left {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 458px;
}

.header__top-right {
  display: flex;
  align-items: center;
}

.header__banner {
  display: none;
}

.operating-time {
  margin-right: 35px;
}

.operating-time__text {
  display: block;
  max-width: 195px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #828282;
}

.operating-time span {
  display: inline-block;
  margin-left: 9px;
}

.btn-request {
  display: flex;
  align-items: center;
  padding: 15px 28px 17px 28px;
  box-shadow: 0px 10px 15px #E5E5E5;
  opacity: 0.99;
  background: linear-gradient(0deg, #005AA5 0.06%, #1E91CD 99.97%);
  border-radius: 100px;
}

.btn-request span {
  margin-left: 12px;
  display: inline-block;
  font-family: 'Montserrat', 'sans-serif';
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  color: #FFFFFF;
}

.btn-request:hover {
  background: #045FA8;
  opacity: 0.99;
}

.swiper-container {
  position: relative;
  margin: 0 auto;
  margin-top: 20px;
}

.swiper-slide img {
  width: 100%;
}

.button-prev,
.button-next {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  z-index: 10;
}

.button-next {
  top: 50%;
  transform: translateY(-50%) rotate(180deg);
  right: 0;
}

.main {
  margin-top: 62px;
}

.info-cards {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 83px;
}

.info-cards__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 370px;
  padding: 30px;
  border: 1px solid #CFDAE1;
  border-radius: 5px;
}

.info-card__text {
  margin-left: 25px;
}

.info-card__text h4 {
  margin: 0;
  margin-bottom: 8px;
}

.info-card__text p {
  margin: 0;
}

.product-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.product-info__img img {
  width: 100%;
  height: 100%;
}

.description {
  max-width: 767px;
}

.description__title {
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 38px;
  color: #171719;
}

.description__text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #171719;
}

.description__text:not(:last-child) {
  margin-bottom: 30px;
}

.description__text--sub-text {
  ont-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #D42828;
}

.sub-descr {
  margin-top: 46px;
}

.form-request {
  position: relative;
  padding: 92px 0 92px 60px;
  margin-top: 100px;
  background: linear-gradient(151.01deg, #FFF1EB 15.35%, #C4E9FF 160.88%);
  border-radius: 3px;
}

.form-request__img {
  position: absolute;
  width: 352px;
  height: 376px;
  top: -40px;
  right: 0;
  background-image: url("/img/girl.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: 50;
}

.form-request__text {
  display: flex;
  align-items: center;
}

.form-request__text h6 {
  margin: 0;
  margin-right: 5px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #171719;
  opacity: 0.99;
}

.form-request__text span {
  padding: 0 5px 0 5px;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #FFFFFF;
  background: #D22626;
  border-radius: 3px;
}

.form-request__title {
  margin: 15px 0 29px 0;
  font-style: normal;
  font-weight: 600;
  font-size: 34px;
  line-height: 36px;
  color: #171719;
}

.form {
  display: flex;
  align-items: center;
}

.form__input {
  margin-right: 12px;
  padding: 15px;
  width: 170px;
  border: none;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #171719;
  background: #FFFFFF;
  border-radius: 3px;
}

.form__input::placeholder {
  color: #171719;
}

.form__btn {
  padding: 17px 25px 17px 25px;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  color: #FFFFFF;
  background: linear-gradient(0deg, #005AA5 0.06%, #1E91CD 99.97%);
  box-shadow: 0px 10px 15px #E5E5E5;
  border-radius: 3px;
}

.form__btn:active {
  background: linear-gradient(0deg, #006AA5 0.06%, #1E96CD 99.97%);
}

.form__btn:hover {
  background: #045FA8;
  opacity: 0.99;
}

.select {
  position: relative;
  margin-right: 12px;
  cursor: pointer;
  max-width: 200px;
  border-radius: 3px;
}

.select__header {
  padding: 15px;
  display: flex;
  align-items: center;
  background: #FFFFFF;
  border-radius: 3px;
}

.select__current {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #171719;
}

.select__icon {
  display: inline-block;
  margin-left: 28px;
  width: 10px;
  height: 5px;
  background-image: url("../img/arrow-select.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.select__item {
  padding: 15px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #171719;
  background: #FFFFFF;
  border-radius: 3px;
}

.select__body {
  position: absolute;
  display: none;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
}

.select.open .select__body {
  display: block;
}

.footer {
  margin-top: 104px;
  padding: 36px 0 36px 0;
  background: #FBFBFD;
}

.footer__container {
  margin: 0 auto;
  max-width: 1210px;
}

.footer__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__info p {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
}

.footer__info a {
  text-decoration: none;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #005AA5;
  border-bottom: 1px solid #005AA5;
}

@media (max-width: 1163px) {
  .product-info {
    flex-direction: column;
  }
  .product-info__img {
    width: 600px;
  }
}

@media (max-width: 1145px) {
  .info-cards {
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 45px;
  }
  .info-cards__item {
    margin: 10px;
  }
  .form-request__img {
    display: none;
  }
}

@media (max-width: 985px) {
  .header__top-left {
    width: auto;
  }
  .header__top-left .rewards {
    display: none;
  }
}

@media (max-width: 875px) {
  .form__input {
    margin-right: 0;
    width: 100%;
  }
  .form__input:not(:last-child) {
    margin-bottom: 14px;
  }
  .select {
    margin-right: 0;
    width: 100%;
    max-width: 100%;
    margin-bottom: 14px;
  }
  .select__icon {
    margin-left: auto;
  }
  .form-request {
    padding: 20px 15px 20px 15px;
  }
  .form-request__text {
    justify-content: center;
  }
  .form-request__title {
    text-align: center;
    justify-content: center;
    font-size: 26px;
  }
  .form-request__form {
    flex-direction: column;
  }
  .footer {
    margin-top: 20px;
  }
  .footer__info {
    flex-wrap: wrap;
  }
}

@media (max-width: 773px) {
  .btn-request {
    padding: 10px 23px 12px;
  }
  .btn-request span {
    font-size: 14px;
  }
  .product-info__img {
    width: 100%;
  }
}

@media (max-width: 773px) {
  .container {
    padding: 0;
  }
  .header {
    margin-top: 0;
  }
  .header__top {
    padding: 10px;
    background: #FFFFFF;
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.15);
  }
  .operating-time {
    display: none;
  }
  .btn-request {
    margin-left: 10px;
    margin-right: 10px;
  }
}

@media (max-width: 604px) {
  .header__slider {
    display: none;
  }
  .header__banner {
    margin-top: 20px;
    display: block;
    text-align: center;
    width: 100%;
  }
  .main {
    margin-top: 0px;
  }
}

@media (max-width: 500px) {
  .logo {
    width: 200px;
  }
  .btn-request {
    padding: 7px 20px 9px;
  }
  .btn-request span {
    font-size: 12px;
  }
}

@media (max-width: 432px) {
  .info-cards__item {
    width: 290px;
    height: 120px;
  }
  .info-cards__item img {
    width: 82px;
    height: 70px;
  }
  .info-card__text h4 {
    font-size: 15px;
    line-height: 24px;
  }
  .info-card__text p {
    font-size: 10px;
    line-height: 18px;
  }
  .logo {
    width: 100px;
  }
  .btn-request {
    padding: 4px 16px 6px;
  }
  .btn-request span {
    font-size: 9px;
  }
}
